"use client"
import axios from "axios";
import Link from "next/link"
import { useRouter } from "next/navigation";
import React from "react";
import { toast } from "react-hot-toast"

export default function ProfilePage() {
    const router = useRouter()
    const [data, setData] = React.useState("")

    const logout = async () => {
        try {
            const response = await axios.get('/api/users/logout')
            toast.success("Logout Success")
            router.push("/login")
        }
        catch (error: any) {
            console.log(error.message)
            toast.error("Logout Failed")
        }
    }

    const getUserDetails = async () => {
        const response = await axios.get('/api/users/me')
        console.log(response.data)
        setData(response.data.data._id)
    }

    return (
        <div className="flex flex-col items-center justify-center min-h-screen py-2">
            <h1>Profile</h1>
            <hr />
            <p>Profile Page</p>
            <h2 className="p-1 m-3 rounded bg-green-500">{data === "" ? "Nothing" : <Link href={`/profile/${data}`}>{data}</Link>}</h2>
            <button onClick={getUserDetails} className="bg-green-800 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded justify-center items-right mb-4">Get User Details</button>
            <button onClick={logout} className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded justify-center items-right mb-4">Logout</button>
        </div>
    )
}
